import React, { useState } from "react";
import "../../../shared/settings.css";
import { AiFillEdit, AiFillClockCircle } from "react-icons/ai";
import AdminSettingsHeader from "../../../components/AdminSettingsHeader";
import Modal from "../../../components/Modal";
import { Button, FormGroup, Input } from "mtforms";
import { useGetSettingById, useGetSettings, useUpdateSettings } from "../hooks";
import { toast } from "react-toastify";
import { errorAlert, toastOptions } from "../../../utils";
import { convertToAMPM } from "./util";
import Layout from "../../../layout";

const Users = () => {
  const [open, setOpen] = useState(false);
  const [formData, setFormData] = useState({});
  const [errors, setErrors] = useState({});
  const { data: settingsID } = useGetSettings();
  const ID = settingsID && settingsID[0].id;
  const { data } = useGetSettingById(ID);

  const openHandler = () => {
    setOpen(true);
    setFormData(data?.data);
  };

  const handleChange = (name, value) => {
    setFormData({ ...formData, [name]: value });
  };
  const validationHandler = (name, error) => {
    setErrors({ ...errors, [name]: error });
  };

  const { mutate, isError, isSuccess, reset, error } = useUpdateSettings();

  const submitHandler = () => {
    const newData = { ...formData, id: data?.data?.id };
    mutate(newData);
  };
  if (isError) {
    reset();
    errorAlert(error);
  }
  if (isSuccess) {
    reset();
    setOpen(false);
    toast.success(`Time Updated Successfully`, toastOptions);
  }

  console.log({ formData });
  return (
    <Layout name="Settings" title="Settings">
      <div className="settings_container">
        <div className="settings_container_layout">
          <div className="setting_container">
            <AdminSettingsHeader time={"remote"} />
            <div className="title">
              <span className="ams__icon">
                <AiFillClockCircle />
              </span>
              Office Hours
              <span className="companyName">
                <AiFillEdit className="ams__icon_btns" onClick={openHandler} />
              </span>
            </div>
            <div className="title">
              <h5>Opening Time : {convertToAMPM(data?.data?.opens)} </h5>
              <h5>Closing Time : {convertToAMPM(data?.data?.closes)} </h5>
              <h5>
                Clock In Window : {data?.data?.opening_window || 0} Minutes{" "}
              </h5>
              <h5>
                Clock Out Window : {data?.data?.closing_window || 0} Minutes{" "}
              </h5>
            </div>
            <Modal
              isVisible={open}
              title={""}
              size="md"
              content={
                <div className="modal_form_container">
                  <FormGroup
                    onSubmit={submitHandler}
                    validation={formData}
                    errors={errors}
                    setErrors={setErrors}
                  >
                    <Input
                      name="opens"
                      label="Opening Time"
                      value={formData["opens"]}
                      onChange={handleChange}
                      type="time"
                      validationHandler={validationHandler}
                      error={errors.opens}
                      required={true}
                      readOnly={true}
                      size="large"
                      className="formInput"
                    />

                    <Input
                      name="closes"
                      label="Closing Time"
                      value={formData["closes"]}
                      onChange={handleChange}
                      type="time"
                      validationHandler={validationHandler}
                      error={errors.closes}
                      required={true}
                      readOnly={true}
                      size="large"
                      className="formInput"
                    />

                    <Input
                      name="opening_window"
                      label="Clock In Window (Mins)"
                      value={formData["opening_window"]}
                      onChange={handleChange}
                      type="number"
                      validationHandler={validationHandler}
                      error={errors.opening_window}
                      required={true}
                      readOnly={true}
                      size="large"
                      className="formInput"
                    />

                    <Input
                      name="closing_window"
                      label="Clock Out Window (Mins)"
                      value={formData["closing_window"]}
                      onChange={handleChange}
                      type="number"
                      validationHandler={validationHandler}
                      error={errors.closing_window}
                      required={true}
                      readOnly={true}
                      size="large"
                      className="formInput"
                    />

                    <Button
                      size="large"
                      type="submit"
                      title="Save"
                      className="formButton"
                    />
                  </FormGroup>
                </div>
              }
              onClose={() => setOpen(false)}
              footer=""
            />
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Users;
